export default {
	data() {
		return {
			schoolId: '',
			tabs: 0,
			listData: [],
			photoData: ['http://jkdq.521che.com/uploads/schoolImage20190918/5d81db86d9662.jpg'],
			current: 0,
			tabsData: ['班型', '教练', '场地', '评价'],
			num: '2324',
			collect: false,
			classListData: [],
			areaListData: [],
			maskOpen: false,
			user: '',
			tel: '',
			Drivinglicense: '',
			array: ['C1', 'C2', 'C3', 'A1', 'A2', 'A3', 'B1', 'B2'],
			location: 0,
		}
	},
	onLoad: function(options) {
		console.log(options.id);
		this.schoolId = options.id
		//驾校信息
		uni.request({
			url: this.$Url + '/api/school/details?id=' + options.id,
			method: 'get',
			data: {},
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			success: (res) => {
				if (res.data.code == 200) {
					this.listData = res.data.msg
					let arr = [],
						data = JSON.parse(this.listData[0].showImg)
					for (let i in data) {
						data[i] = this.$Url + data[i]
						arr.push(data[i]);
					}
					if (arr.length > 0) {
						this.photoData = arr
					}
				} else {
					uni.showToast({
						icon: 'none',
						title: '网络不给力，请稍后重试',
						duration: 2000
					});
				}
			}
		});

		// 班级
		uni.request({
			url: this.$Url + '/api/school/course/list',
			method: 'get',
			data: {
				schoolId: options.id
			},
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			success: (res) => {
				if (res.data.code == 200) {
					this.classListData = res.data.msg
				} else {
					uni.showToast({
						icon: 'none',
						title: '网络不给力，请稍后重试',
						duration: 2000
					});
				}
			}
		});

		//场次
		uni.request({
			url: this.$Url + '/api/school/field/list',
			method: 'get',
			data: {
				schoolId: options.id
			},
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			success: (res) => {
				if (res.data.code == 200) {
					var arr = []
					for (let i in res.data.msg) {
						res.data.msg[i].showImg = this.$Url + res.data.msg[i].showImg
						arr.push(res.data.msg[i]);
					}
					this.areaListData = arr
				} else {
					uni.showToast({
						icon: 'none',
						title: '网络不给力，请稍后重试',
						duration: 2000
					});
				}
			}
		});
		//检查是否收藏
		if (uni.getStorageSync('logon_status') == 1) {
			uni.request({
				url: this.$Url + '/api/v1/exam/collection/check',
				method: 'POST',
				data: {
					'token': uni.getStorageSync('token'),
					'schoolId': options.id
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: (res) => {
					if (res.data.code == 200) {
						if (res.data.msg != null) {
							this.collect = true
						} else {
							this.collect = false
						}
					} else {
						uni.showToast({
							icon: 'none',
							title: '网络不给力，请稍后重试',
							duration: 2000
						});
					}
				}
			});
		}
	},
	onNavigationBarButtonTap: function(e) {
		if (e.type == 'menu') {
			uni.showActionSheet({
				title: '',
				itemList: ['纠错', '转照片', '评价', '绑定驾校', '浏览记录', '分享'],
				success: (e) => {
					uni.showToast({
						title: "点击了第" + e.tapIndex + "个选项",
						icon: "none"
					})
				}
			})
		}
	},
	methods: {
		tapTabs: function(e) {
			this.tabs = e.currentTarget.dataset.idx
		},
		changeCurrent: function(e) {
			this.current = e.detail.current
		},
		tapCollect: function(e) {
			if (uni.getStorageSync('logon_status') == 1) {
				this.collect = !this.collect
				if (this.collect) {
					uni.request({
						url: this.$Url + '/api/v1/exam/collection/store',
						method: 'POST',
						data: {
							'token': uni.getStorageSync('token'),
							'schoolId': this.schoolId
						},
						header: {
							'content-type': 'application/x-www-form-urlencoded'
						},
						success: (res) => {
							if (res.data.code == 200) {
								uni.showToast({
									title: '收藏成功',
									duration: 1000
								});
							} else {
								uni.showToast({
									icon: 'none',
									title: '网络不给力，请稍后重试',
									duration: 2000
								});
							}
						}
					});
				} else {
					uni.request({
						url: this.$Url + '/api/v1/exam/collection/cancel',
						method: 'POST',
						data: {
							'token': uni.getStorageSync('token'),
							'schoolId': this.schoolId
						},
						header: {
							'content-type': 'application/x-www-form-urlencoded'
						},
						success: (res) => {
							if (res.data.code == 200) {
								uni.showToast({
									title: '取消成功',
									duration: 1000
								});
							} else {
								uni.showToast({
									icon: 'none',
									title: '网络不给力，请稍后重试',
									duration: 2000
								});
							}
						}
					});
				}
			} else {
				uni.navigateTo({
					url: '../../login/login?form=3'
				});
			}

		},
		openLocation: function(e) {
			uni.openLocation({
				longitude: Number(this.listData[0].coordinate.split(",")[0]),
				latitude: Number(this.listData[0].coordinate.split(",")[1]),
				name: this.listData[0].schoolName,
				address: this.listData[0].address
			})

		},
		tapMap: function(e) {
			uni.openLocation({
				longitude: Number(this.areaListData[e].coordinate.split(",")[0]),
				latitude: Number(this.areaListData[e].coordinate.split(",")[1]),
				name: this.areaListData[e].fieldName,
				address: this.areaListData[e].address
			})
		},
		tapMaskOpen: function(e) {
			this.maskOpen = !this.maskOpen
		},
		tapUser: function(e) {
			this.user = e.detail.value
		},
		tapTel: function(e) {
			this.tel = e.detail.value
		},
		bindPickerChange: function(e) {
			this.Drivinglicense = this.array[e.target.value]
		},
		chooseLocation: function() {
			uni.chooseLocation({
				success: (res) => {
					this.location = res.address
				}
			})
		},
		formSubmit: function(e) {
			console.log(this.user.length);
			if (this.user.length < 1) {
				uni.showToast({
					icon: 'none',
					title: '请输入真实姓名',
					duration: 2000
				});
			} else if (this.tel.length < 1) {
				uni.showToast({
					icon: 'none',
					title: '请输入手机号',
					duration: 2000
				});
			} else if (!(/^1[3456789]\d{9}$/.test(this.tel))) {
				uni.showToast({
					icon: 'none',
					title: '手机号码有误',
					duration: 2000
				});
			} else if (this.Drivinglicense.length < 1) {
				uni.showToast({
					icon: 'none',
					title: '请选择驾照类型',
					duration: 2000
				});
			} else if (this.location == 0) {
				uni.showToast({
					icon: 'none',
					title: '请选择地址',
					duration: 2000
				});
			} else {
				console.log(this.user, this.tel, this.Drivinglicense, this.location)
				this.maskOpen = !this.maskOpen
				uni.showToast({
					icon: 'success',
					title: '提交成功！',
					duration: 2000
				});
				uni.request({
					url: this.$Url + '/api/school/inquiry',
					method: 'GET',
					data: {
						schoolId: this.schoolId,
						name: this.user,
						start: this.location,
						phone: this.tel,
						type: this.Drivinglicense
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: (res) => {
						if (res.data.code == 200) {
							uni.showToast({
								title: res.data.msg,
								duration: 1000
							});
						} else {
							uni.showToast({
								icon: 'none',
								title: '网络不给力，请稍后重试',
								duration: 2000
							});
						}
					}
				});
			}

		}
	}
}
